@import url(https://fonts.googleapis.com/css?family=Roboto);
@font-face {
  src: url(/portfolio/static/media/light.acca77cc.woff);
  font-family: light;
}
@font-face {
  src: url(/portfolio/static/media/medium.da7bec02.woff);
  font-family: medium;
}
@font-face {
  src: url(/portfolio/static/media/heavy.ad738b8f.woff);
  font-family: heavy;
}
*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}
body {
  font-size: 1.4rem;
  font-family: heavy;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #22222a;
  height: 100vh;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes activeColor {
  from{
      
  }to {
      background-color: #f68b9d;
  }
}

@keyframes activeColor {
  from{
      
  }to {
      background-color: #f68b9d;
  }
}

@media only screen and (min-width:200px) {
    
  html {
      font-size: 65%;
  }
  .mobile-hide {
      display: none;
  }
  .desktop-hide  {
      flex-basis: 0 !important;
  }
  .Menu-nav {
      width: 90%;
  }
  .profession {
    margin-bottom: 3rem;
}
.body {
    width: 100% !important;
    margin-bottom: 2rem !important;
}
.Languages-home > div {
    flex-basis: auto;
    font-size: 1rem;
    
}
.Languages-home >div:not(:last-child) {
    margin-right: 1rem;
}

}
@media only screen and (min-width:750px) {
  html {
      font-size: 90%;
  }
}
@media only screen and (min-width:950px) {
  html {
      font-size: 62.5%;
  }
  .desktop-hide {
      display: none;
  }
  .mobile-hide {
      display: block !important;
  }
  .mobile-hide-plus {
      visibility: visible !important;
  }
  .Menu-nav {
      width:80%;
  }
  .body {
      overflow: hidden !important;
      width: 80% !important;
  }

}

.Left-side {
    position: relative;
    flex: 1 1;
}
.image-container {
    position: relative;
    width: 0;
    background-image: url(/portfolio/static/media/c.1ef7cbe8.jpg);
    height: 100%;
    background-size: cover;
    background-position: center;
    -webkit-transition:  all 0.5s ease-in-out;
    transition:  all 0.5s ease-in-out;
    /*  width:0  */
}
.image-container::after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(17, 17, 17, 0.4);
}
.image-title {
    position: absolute;
    top: 40%;
    left: -5%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-size: 12rem;
    color: #fff;
    z-index: 2;
    -webkit-transition:  all 0.4s ease-out;
    transition:  all 0.4s ease-out;
    opacity:0;
}
.image-extra {
    width: 75% !important;
}
.text-extra {
    opacity: 1 !important;
}
@media only screen and (min-width:950px) {
    .image-title {
        font-size: 8.5rem;
    }
}
.Right-side {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
}
.flex-middle {
    margin-top: -5rem;
    overflow: hidden;
    height: 0rem;
    -webkit-transition: all 0.5s ease-out;
    transition: all 0.5s ease-out;
}
.year {
    margin-bottom: 1rem;
    font-family: light;
    font-size: 1.1rem;
    color: #bdbdd5;
}
.profession {
    margin-bottom: 5rem;
    font-family: light;
    font-size: 1rem;
    color:#707086;
    text-transform: uppercase;
}
.body {
    color: #bdbdd5;
    width: 80%;
    line-height: 2.2rem;
    font-size: 1.4rem;
    margin-bottom: 5rem;
}
.Languages-home {
    display: flex;
}
.Languages-title {
    font-family: light;
    color: #707086;
    margin-right: 0.5rem;
}
.Languages-home > div {
    flex-basis: 32%;
    text-transform: uppercase;
    font-size: 1.1rem;
    color: #bdbdd5;
}
.right-extra {
    height: 30rem !important;
}


.Flex-container {
    display: flex;
    justify-content: center;
    flex:1 1;
}
.image-mobile{
    width: 0;
    height: 100vh;
    background-image: url(/portfolio/static/media/c.1ef7cbe8.jpg);
    background-size: cover;
    background-position: center;
    -webkit-transition:  all 0.5s ease-in-out;
    transition:  all 0.5s ease-in-out;
    position: absolute;
}
.image-mobile::after{
  content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(17, 17, 17, 0.8);
} 
.mobile-imageExtra {
    width: 100% !important;
}
.Work {
    width: 80%;
    max-width: 134rem;
    margin: 0 auto;
    position: absolute;
    z-index: 2;
    top: 6.5rem;
    bottom: 7rem;
}


.project {
    position: absolute;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    white-space: nowrap;
    overflow: hidden;
    -webkit-transition:  all 0.4s ease-out;
    transition:  all 0.4s ease-out;
    width: 0rem;
    color: #fff; 
}

.project-extra {
    width: 20rem !important;
}
.Project-title {
    font-family: light;
    font-size: 1.1rem;
    margin-bottom: 1rem;
    text-transform: uppercase;
    -webkit-transition: all 0.4s ease;
    transition: all 0.4s ease;
}
.Languages {
    color: rgb(201, 201, 201);
    font-family: light;
    font-size: 1rem;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    height: 20px;
}
.Languages-list,.second-title {
    -webkit-transition:  all 0.4s ease;
    transition:  all 0.4s ease;
   font-size: 1rem;
}
 a {
    text-decoration: none;

}

.project:hover .Languages-list{
    -webkit-transform:translateY(-15px);
            transform:translateY(-15px);
}
.project:hover .second-title {
    -webkit-transform:translateY(-15px);
            transform:translateY(-15px);
}
.second-title {
    -webkit-transform:translateY(15px);
            transform:translateY(15px);
    color: #fff;
}

.row {
    position: relative;
    width: 100%;
    height: 100%;

}

.flex {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 15rem;
}
.flex-box {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  position: relative;
  z-index: 3;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  -webkit-transform: translateY(50px);
          transform: translateY(50px);
  opacity: 0;
}
.flex-box:nth-child(2) {
  -webkit-transition-delay: 0.1s;
          transition-delay: 0.1s;
}
.flex-box:nth-child(3) {
  -webkit-transition-delay: 0.2s;
          transition-delay: 0.2s;
}
.flex-box:not(:last-child) {
  margin-bottom: 15rem;
}
.extra-flexbox {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.About-title {
  flex-basis: 16.5%;
  font-family: light;
  color: #bdbdd5;
  font-size: 1.1rem;
  text-transform: uppercase;
  margin-top: 7px;
}

.About-description {
  padding-right: 15rem;
  flex-basis: 66.7%;
  line-height: 2.7rem;
  color: #c2c2dd;
  font-size: 1.5rem;
  font-family: "Roboto", sans-serif;
}
.About-skills {
  font-size: 1.5rem;
  font-family: "Roboto", sans-serif;
  flex-basis: 16.7%;
  display: flex;
  flex-direction: column;
  color: #c2c2dd;
}
.About-skills span:first-child {
  margin-bottom: 1.5rem;
}
.line {
  width: 10rem;
  height: 1px;
  background-color: #c2c2dd;
  display: inline-block;
}
.skills-flex {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5rem;
}

@media (max-width: 950px) and (min-width: 200px) {
  .flex {
    margin-top: 2rem;
  }
  .flex-box {
    justify-content: space-between;
  }
  .flex-box:not(:last-child) {
    margin-bottom: 5rem;
  }
  .About-title:last-child {
    margin-right: 1rem !important;
  }
  .About-title {
    margin-top: 0.5rem;
  }
  .About-description {
    width: 90% !important;
    padding: 0 !important;
    flex-basis: auto !important;
  }
}

.Menu-nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    top: 0;
    padding: 2.5rem 0;
    z-index: 1000;
    width: 80%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    max-width: 134rem;

}
.Logo {
    margin-right: auto;
    color: #fff;
    cursor: pointer;
}

.Menu-nav li {
    flex-basis: 16.64%;
    list-style-type: none;
}


.Menu-nav a {
    text-decoration: none;
    font-family: heavy;
    color: #fff;
    text-transform: lowercase;
    

}
header li span {
    color: #fff;
    cursor: pointer;
}
.active {
    position: relative;
}
.active::after{
    position: absolute;
    content: '';
  
    top:50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    right: -1.5rem;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    -webkit-animation:activeColor .5s;
            animation:activeColor .5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;

}
.burger > div {
        width: 30px;
        height: 2px;
        border-radius: 20px;
        background-color: #fff;
        margin-bottom: 4px;
        cursor: pointer;
}
.mobile-menu {
    position: fixed;
    top: 0;
    width: 0;
    right: 0;
    bottom: 0;
    background-color: #22222A;
    z-index: 1500;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
   
}
.x-mobileMenu {
    color:#fff;
    text-align: right;
    margin-right: 5rem;
    margin-top: 5rem;
    cursor: pointer;
}
.mobile-menu ul {
    list-style-type: none;
    margin-top: 10rem;
    margin-left: 5rem;
}
.mobile-menu ul li:not(:last-child) {
    margin-bottom: 4rem;
}
.mobile-menu ul li a{
    font-size: 5rem;
    text-decoration: none;
    color: #fff;
}
.mobileMenuActive {
    width: 100%;
}

.Lines {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width:80%;
    left:50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    max-width: 134rem;
}
.Line {
    position: absolute;
    top: 0;
    width: 1px;
    height: 100%;
    background-color: #fff;
    left: 50%;
    opacity: 0.1;
   
}

 .Line:nth-child(1) {
        left: 0;
}
.Line:nth-child(2) {
    left: 16.67%;
}
.Line:nth-child(3) {
    left: 33.32%;
}
.Line:nth-child(4) {
    left: 49.98%;
}
.Line:nth-child(5) {
    left: 66.7%;
}
.Line:nth-child(6) {
    left: 83.3%;
}
.Line:nth-child(7) {
    left: 99.88%;
} 


.Plus {
    position: absolute;
    top: 6.5rem;
    left: 0;
    bottom: 7rem;
    right: 0;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
.Pluses {
    display: flex;
    justify-content: space-between;
    align-items: center;
    -webkit-transition:  all 0.5s ease-in;
    transition:  all 0.5s ease-in;

}
.Pluses:nth-child(3) {
    opacity: 0;
}
.visible {
    opacity: 1 !important;
}
.Pluses > div {
    width: 8px;
    height: 8px;
    position: relative;
}
.Pluses > div::after {
    position: absolute;
    content: '';
    left: 0;
    top: 50%;
    width: 100%;
   height: 1px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%); 
    background-color: #595971;
}.Pluses > div::before {
    position: absolute;
    content: '';
    top: 0;
    left: 50%;
    width: 1px;
    height: 100%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    background-color: #595971;
}
.Pluses > div:nth-child(1) {
 left: -4px;
}.Pluses > div:nth-child(2) {
    left: -3px;
}.Pluses > div:nth-child(3) {
    left: -2px;
}.Pluses > div:nth-child(4) {
    left: 0px;
}.Pluses > div:nth-child(5) {
    left: 1px;
}.Pluses > div:nth-child(6) {
    left: 2px;
}.Pluses > div:nth-child(7) {
    left: 2px;
}
@media only screen and (min-width:200px) {
    .mobile-hide-plus {
        visibility: hidden;
    }

}
.Overlay {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 0;
    background-color:#101015;
    z-index: -1;
    -webkit-transition:  all 0.7s cubic-bezier(.86,0,.07,1);
    transition:  all 0.7s cubic-bezier(.86,0,.07,1);
}
.overlay-extra {
    width: 32% !important;
}
